body {
  margin: 0;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: flex;
}

.stopwatch__watch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move;
  position: absolute;
  bottom: 50%;
  left: 38.5vmin;
}

.stopwatch__second-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  position: absolute;
  bottom: 50%;
  left: 39.25vmin;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  bottom: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.stopwatch__minute-hand--speed-up {
  animation-duration: 600s;
  animation-timing-function: steps(60, end);
}

.stopwatch__second-hand--speed-up {
  animation-duration: 10s;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

